html{height:100%}body{font-family:"Roboto", sans-serif;font-size:16px;height:100%;padding:0 8px}.main-nav{padding:20px 20px 0;background:rgba(255,255,255,0.8);margin:0 auto;z-index:100;display:flex}.main-nav a{top:8px;right:6px;padding:8px 12px;color:#14213d;line-height:1.35;border-radius:3px;transition:0.25s all;text-decoration:none}.main-nav a:hover{font-weight:bold;color:#14213d}.main-nav .push-right{margin-left:auto}.main-nav .email{font-weight:bold}.main-nav .email i{margin-right:24px}.profile{display:flex;flex-direction:column;padding-top:120px;padding-bottom:20px;align-items:center}.profile .profile-photo{width:160px;height:160px;border-radius:80px}.profile .intro-name{font-size:48px;margin-bottom:0;font-weight:lighter}.profile .intro{margin-top:16px;color:#fca311;font-size:64px}.profile .desc{color:#28417b}
